.nav__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 175px 0 0;
  padding: 0;
  list-style-type: none;

  @include vp-1199 {
    margin: 0 31px 0 0;
  }

  @include vp-767 {
    display: block;
    column-count: 2;
    column-gap: 30px;
    padding: 0;
    margin: 0;
  }
}

.nav__logo {
  margin-right: auto;
  padding-right: 15px;
  display: flex;
  flex-direction: column;
  max-width: 150px;
  gap: 10px;

  @include vp-767 {
    position: static;
    margin-bottom: 11px;
  }
}

.nav__item {
  margin: 0;
  margin-right: 30px;
  padding: 0;

  @include vp-1199 {
    margin-right: 20px;
  }

  @include vp-767 {
    margin-right: 0;
    margin-bottom: 11px;
  }

  &:last-child {
    margin-right: 0;
  }
}

.nav__link {
  display: inline-block;
  margin: 0;
  padding: 7px 5px;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: $color-default-white;

  @include vp-1199 {
    padding: 5px;
  }

  @include vp-767 {
    padding: 5px 0;
    font-size: 16px;
    line-height: 19px;
  }
}
