.staff {
  padding: 0;
  padding-top: 75px;
  padding-bottom: 141px;
  width: 100%;

  @include vp-1199 {
    padding-top: 70px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding-top: 34px;
    padding-bottom: 70px;
  }

  .container {
    position: relative;
  }

  .staff__container {
    padding: 0 60px;
    max-width: 1280px;

    @include vp-1199 {
      padding: 0 45px;
    }

    @include vp-767 {
      padding: 0 15px;
    }
  }

  h2 {
    box-sizing: border-box;
    width: 100%;
    max-width: 1366px;
    margin: 0 auto;
    margin-bottom: 13px;
    padding: 0 103px 0 105px;
    font-family: "Alumni Sans", "Arial", sans-serif;
    font-weight: 600;
    font-size: 54px;
    line-height: 65px;
    letter-spacing: 0.03em;

    @include vp-1199 {
      font-size: 42px;
      line-height: 50px;
      padding: 0 45px;
      margin-bottom: 0;
      text-align: center;
      transform: translateX(-3px);
    }

    @include vp-767 {
      font-size: 32px;
      line-height: 38px;
      padding: 0 15px;
      margin-bottom: 10px;
    }
  }
}

.staff__slider {
  position: relative;
  z-index: 2;
  padding-top: 35px;
  width: 100%;

  @include vp-1199 {
    padding-top: 32px;
    width: calc(100% - 112px);
  }

  @include vp-767 {
    width: calc(100% - 44px);
  }
}

.staff__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.staff__item {
  width: 22.4%;

  @include vp-1199 {
    width: 47.35%;
  }

  @include vp-767 {
    width: 91.9%;
  }
}

.staff__slider-buttons {
  position: absolute;
  z-index: 0;
  top: 50%;
  left: 0;
  width: 100%;
  height: 40px;
  transform: translateY(-50%);
}


.staff__slider-button {
  position: absolute;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: 0;
  width: 20px;
  height: 40px;
  background: transparent;
  color: $color-dark-grey;
  border: none;

  @include vp-767 {
    svg {
      width: 12px;
      height: 21px;
    }
  }

  &--prev {
    left: 1px;
    transform: rotate(180deg) translateY(-22px);

    @include vp-1199 {
      left: 43px;
      transform: rotate(180deg) translateY(-18px);
    }

    @include vp-767 {
      left: 10px;
      transform: rotate(180deg) translateY(-19px);
    }
  }

  &--next {
    right: 1px;
    transform: translateY(22px);

    @include vp-1199 {
      right: 43px;
      transform: translateY(18px);
    }

    @include vp-767 {
      right: 10px;
      transform: translateY(18px);
    }
  }

  @include hover-focus {
    filter: drop-shadow(0 0 4px $color-cobalt-number);
    color: $color-cobalt-number;
  }

  &:active {
    opacity: 0.5;
  }
}

.staff__list-wrapper {
  width: 100%;
}

.staff__list-wrapper > div {
  position: relative;
  width: 100%;
  padding-bottom: 113.1%;

  @include vp-1199 {
    padding-bottom: 109.7%;
  }

  @include vp-767 {
    padding-bottom: 121.2%;
  }
}

.staff__list-wrapper > div > div {
  position: absolute;
  width: 100%;
  height: 100%;
}
