.tabs {
  max-width: 100%;
  display: flex;
  flex-direction: column;
}

.tabs__controls {
  position: relative;
  display: inline-flex;
  flex-wrap: wrap;
  padding: 5px 0 8px 0;
  align-self: center;
  margin-bottom: 45px;

  @include vp-1199 {
    margin-bottom: 52px;
  }

  @include vp-767 {
    flex-wrap: wrap;
    padding-bottom: 9px;
    margin-bottom: 41px;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 2px;
    background-color: $color-grey;
    opacity: 0.2;
  }
}

.tabs__control {
  position: relative;
  flex-shrink: 0;
  display: block;
  padding: 5px 0;
  margin: 0;
  margin-right: 46px;
  border: none;
  background-color: transparent;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: rgba($color-sapphire, 0.5);
  cursor: pointer;

  @include vp-1199 {
    font-size: 18px;
    line-height: 27px;
    margin-right: 54px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 24px;
    margin-right: 45px;
  }

  &:last-child {
    margin-right: 0;
  }

  @include hover-focus {
    color: $color-sapphire;
    opacity: 0.9;
    outline: #000000 solid 3px;
  }

  &:disabled {
    pointer-events: none;
    opacity: 0.3;
  }

  &.is-active {
    pointer-events: none;
    color: $color-sapphire;
  }
}

.tabs__control.is-active::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -8px;
  display: block;
  width: 99%;
  height: 2px;
  background-color: $color-crimson;

  @include vp-767 {
    bottom: -9px;
    width: calc(100% + 7px);
  }
}

.tabs__content {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.tabs__element {
  display: none;

  &.is-active {
    position: relative;
    z-index: 5;
    display: block;
    width: 100%;
  }
}
